import React, { useLayoutEffect } from "react";
import { DropdownBox } from "../dropdown";
import {
  VirtualizedList as List,
  VirtualizedListProps,
  VirtualizedListItem,
} from "../list/VirtualizedList";
import { SelectOption } from "../select/SelectOption";
import { StyledProps } from "../_type";
import { SelectProps } from "../select";

export interface TagSelectBoxProps extends StyledProps {
  items: VirtualizedListItem<SelectOption>[];
  width: number;
  onChange: (
    value: string,
    context: { event: React.MouseEvent; option: SelectOption }
  ) => void;
  scheduleUpdate: () => void;
  tips: React.ReactNode;
  currentIndex: number;
  listRef: VirtualizedListProps["virtualizedRef"];
  onScrollBottom: VirtualizedListProps["onScrollBottom"];
}

export function TagSelectBox({
  listRef,
  items,
  onChange,
  width,
  scheduleUpdate,
  className,
  style = {},
  currentIndex,
  onScrollBottom,
}: TagSelectBoxProps) {
  useLayoutEffect(() => {
    scheduleUpdate();
  }, [scheduleUpdate, items]);

  const count = items.length;
  if (count === 0) {
    return null;
  }

  return (
    <DropdownBox
      onClick={e => e.stopPropagation()}
      className={className}
      style={{ ...style, width }}
    >
      <List
        virtualizedRef={listRef}
        type="option"
        items={items.map((item, index) => {
          if (item.type === "option") {
            const { option } = item;
            return {
              ...item,
              props: {
                disabled: option.disabled,
                current: currentIndex % count === index,
                onClick: event => {
                  onChange(option.value, {
                    event,
                    option,
                  });
                },
                tooltip: option.tooltip,
              },
            };
          }
          return item;
        })}
        onScrollBottom={onScrollBottom}
      />
    </DropdownBox>
  );
}

export function getListItems({
  tips,
  options,
}: Pick<SelectProps, "tips" | "options">): VirtualizedListItem<SelectOption>[] {
  const items: VirtualizedListItem<SelectOption>[] = [];
  if (tips) {
    items.push({ type: "tips", key: "__tips", text: tips });
  }

  options.forEach(option => {
    items.push({
      type: "option",
      key: option.value,
      text: typeof option.text === "undefined" ? option.value : option.text,
      option,
    });
  });
  return items;
}
